import "./Home.css";
import ThreeFifths from "../components/ThreeFifths";
import Perks from "../components/Perks";
import TripleCard from "../components/TripleCard";
import Steps from "../components/Steps";
import FourPhotos from "../components/FourPhotos";
import FinalPitch from "../components/FinalPitch";

function Home(){
    return (
        <div>
            <ThreeFifths
                header="Your Ultimate Disney Adventure!"
                text="Ask anyone and theyll tell you their dream vacation is a trip to Disney, Universal, or both! Or maybe its a Disney Cruise, or a relaxing week at Aulani Resort & Spa, or a fabulous trek halfway around the world with Adventures by Disney Thats why we create custom vacation packages that make magical, lifelong memories for you and your family. With Magic Lamp Vacations youll save time. Save hassle. Save stress. And create extra magic youll never forget."
                buttonLabel="Book Your Free Consultation"
                photoGallery="Photo Gallery"
                onClick={() => window.location.pathname = "./mousetermindtravel/contact"}
                button={true}
            />
            <Perks 
                header1="The best resorts in the world just got even better!"
                pitch1="Planning the vacation of a lifetime? Yay! But if you dont know what to plan in the first place, youll end up standing in long lines all day and missing the best attractions and characters. (Boo!) Fortunately, weve got great newswe know the Disney and Universal resorts inside and out, and we want to share our secrets with you. When you work with Magic Lamp Vacations to book your vacation, well walk you through every step of the perfect plan, share all of our best-kept resort secrets, and take care of booking everything for you. (And OMG, when we say everything, we mean EVERYTHING!"
                image1="Placeholder"
                image2="Placeholder"
                header2="We can even give you the VIP treatment and take care of everything except packing your suitcase!"
                pitch2="And whether you want to spend time at Walt Disney World in Orlando or Disneyland in California Take a magical trip with Adventures by Disney Cruise with Disney Cruise Line Relax in luxury at Aulani Resort & Spa in Hawaii, or experience all the thrills of Universal Orlando Nothing beats the Magic Lamp Vacations experience."
                header3="You'll start by telling us what's on your wish list."
                pitch3="Walt himself said, if you can dream it, you can do it! But at Magic Lamp Vacations, if you can dream itwell do it! Well ask for a nominal feejust $25 lets you whistle while WE workand then well get started building your dream vacation for you. Youll save hours of planningseriously, like 30, 40 or more. And because of our expertise, your trip will be filled with soooo much more than you ever could plan on your own. Like Tinkerbell said, All you need is a little faith, trust, and pixie dust."
                image3="Placeholder"
            />
            <TripleCard 
                image1="Placeholder"
                header1="Personalized Recommendations"
                pitch1="Your Magic Lamp Vacations Travel-Planning Genie will grant your familys every wish. No call centers or impersonal one size fits all advicethis is a vacation built from the ground up just for you and your loved ones."
                image2="Placeholder"
                header2="Little Known Tips & Insider Secrets"
                pitch2="Imagine the memories youll make when you ask our die-hard Disney fans to plan for you. Youll save 30+ hours of planning timeand get access to all of our first-hand experience and genie-level tips and tricks."
                image3="Placeholder"
                header3="More Magic In Less Time"
                pitch3="Our only goal is to ensure your dream vacation is as magical and memorable as possible. So well show you exactly how to have double or even triple the funand one-of-a-kind experiences you cant get anywhere else."
            />
            <Steps 
                gears="placeholder"
                label1="Step 1"
                label2="Step 2"
                label3="Step 3"
                label4="Step 4"
            />
            <FourPhotos 
                image1="./images/logo-resize.png"
                text1="Walt Disney World (Orlando)"
                image2="./images/logo-resize.png"
                text2="Disneyland (California)"
                image3="./images/logo-resize.png"
                text3="Disney Cruise"
                image4="./images/logo-resize.png"
                text4="Aulani Spa and Resort"
            />
            <FinalPitch 
                tag="Ready for the dream vacation of a lifetime?"
                buttonTitle="GET STARTED WITH A FREE, NO-OBLIGATION CONSULTATION"
                onClick={() => window.location.pathname = "./mousetermindtravel/contact"}
            />
        </div>
    )
}

export default Home;