import { Outlet, Link } from "react-router-dom";
import './NavBar.css';

function NavBar() {
    return (
        <div>
            <nav className="navbar navbar-expand-lg navbar-light">
                <div className="container-fluid">
                    <Link className="navbar-brand" to="/mousetermindtravel"><img className="logo" src="./images/logo-resize.png" /></Link>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNavDropdown" aria-controls="navbarNavDropdown" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarNavDropdown">
                    <ul style={{position: "relative", left: "50%", transform: "translate(-50%)", fontFamily: "'Acme', sans-serif", fontSize: "1.15em"}} className="navbar-nav">
                        <li className="nav-item dropdown" style={{paddingRight: "5rem"}}>
                            <Link style={{color: "black"}} className="nav-link dropdown-toggle" to="/" id="navbarDropdownMenuLink" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                                Our Destinations
                            </Link>
                            <ul className="dropdown-menu" aria-labelledby="navbarDropdownMenuLink">
                                <li><Link id="link" className="dropdown-item" to="/mousetermindtravel/walt-disney-world">Walt Disney World</Link></li>
                                <li><Link id="link" className="dropdown-item" to="/mousetermindtravel/disneyland">Disneyland</Link></li>
                                <li><Link id="link" className="dropdown-item" to="/mousetermindtravel/disney-cruise-line">Disney Cruise Line</Link></li>
                                <li><Link id="link" className="dropdown-item" to="/mousetermindtravel/aulani-spa-resort">Aulani Spa and Resort</Link></li>
                            </ul>
                        </li>
                        <li className="nav-item" style={{paddingRight: "5rem"}}>
                            <Link id="link" className="nav-link" to="/mousetermindtravel/about">About Us</Link>
                        </li>
                        <li className="nav-item">
                            <Link id="link" className="nav-link" to="/mousetermindtravel/contact">Contact Us</Link>
                        </li>
                    </ul>
                    </div>
                </div>
                <button onClick={() => window.location.pathname = "./mousetermindtravel/contact"} className="consultation">Book Your Free Consultation</button>
            </nav>
            <Outlet />
        </div>
    )
}

export default NavBar;