import { BrowserRouter, Routes, Route, Switch } from "react-router-dom";
import About from './pages/About.js';
import Contact from './pages/Contact.js';
import Home from './pages/Home.js';
import NavBar from './pages/NavBar.js';
import DisneyWorld from "./pages/DisneyWorld.js";
import Disneyland from "./pages/Disneyland.js";
import DisneyCruise from "./pages/DisneyCruise.js";
import Aulani from "./pages/Aulani.js";
import Footer from "./pages/Footer.js";
import NotFoundError from "./NotFoundError.js";

function App() {
  return (
    <>
      <BrowserRouter>
        <Routes>
          <Route path="/mousetermindtravel" element={<NavBar />}>
            <Route index element={<Home />} />
            <Route path="/mousetermindtravel/about" element={<About />} />
            <Route path="/mousetermindtravel/contact" element={<Contact />} />
            <Route path="/mousetermindtravel/walt-disney-world" element={<DisneyWorld />} />
            <Route path="/mousetermindtravel/disneyland" element={<Disneyland />} />
            <Route path="/mousetermindtravel/disney-cruise-line" element={<DisneyCruise />} />
            <Route path="/mousetermindtravel/aulani-spa-resort" element={<Aulani />} />
            <Route path="/mousetermindtravel/not-found-error" element={<NotFoundError />} />
          </Route>
        </Routes>
      </BrowserRouter>
      <Footer />
    </>
  );
}

export default App;
